	export default {
		props: ['rurl', "rdata","itemsShow"],
		data() {
			return {
				dataList: [],
				num:1,
				page:1,
				totlePage:100,
				is_bottom:false,
				text:{
					noneData:""
				}
			};
		},
		mounted(){
			
		},
		methods: {
			addNum_num(obj,event) {
               this.dataList[obj.index].is_add_qtrunk = 0;
			},
			deletItem(obj,) {
               this.dataList[obj.index].is_add_qtrunk = 1;
			},
			showDtail() {

			},
			async render() {
			   this.is_bottom = false;
			   uni.pageScrollTo({
			      scrollTop: 0,
			   	  duration:0
			   });
			   const data = {
			   	page: 1,
			   	...this.rdata
			   }
			   const res = await this.$http({
			   	url: this.rurl,
			   	method: "post",
			   	data: data
			   });
			   console.log(res);
			   this.dataList = res.data;
			   this.num = this.dataList.length;
			   this.totlePage = res.total_page;
			   this.page = 2;
			   if(this.totlePage == 1 ){
				   this.is_bottom = true;
			   }
			},
			async reach() {
				console.log("触底")
			   if(this.page > this.totlePage ){
				  this.is_bottom = true;
			    }else{
					const data = {
						page: this.page,
						...this.rdata
					}
					const res = await this.$http({
						url: this.rurl,
						method: "post",
						data: data
					});
					this.dataList.push(...res.data);
					this.page ++;
				}
				
			}
		}
	}
